<template>
  <div class="done">
    <main>
      <p>{{ msg }}</p>
    </main>
  </div>
</template>

<script>
export default {
  name: 'done',
  data () {
    return {
      msg: '登録ありがとうございました'
    }
  }
}
</script>

<style>
main {
  text-align: center;
  margin-top: 40px;
}
</style>



